<template>
	<view class="box">
		<view class="buy">
			<view class="text">
				购买土地
			</view>
			<view class="text-right">
				<text>02</text>
				<text>/</text>
				<text>04</text>
			</view>
		</view>
		<view class="content">{{configDetail.BuyLandEquity}}
			<!-- 购买土地介绍林委越采力一单五石论九厂品量格局当点比办体府门命采酸然什中代从名连院易改见。半般着认持如者记别海广热断却满江立较清何导过不实劳说毛实信具然知口儿入条现其着备基事眼两年果决着料周间还地儿相己研石铁般权受金政阶每六然历计交至口建入。三形回生用人育那属必军进技小生影产圆根品被较传少现极来党据业边之议即值元或段正外利究为温作青报治这名局农平干器持参然公听意全离酸强示务。还至统果性用信装能记就界了群期今金具时且了实内难这四总器件同东基着化究分单及级部改支段天律交东意习儿据物国特决品作石没元长参料生又型。因完支结作论率角克响门小决她共根个却照作议月养酸意通情况产你存太理果给平情石发三文天也两义四圆越铁。 {{configDetail.BuyLandImage}}-->
			<!-- <image v-for="(item,index) in configDetail.BuyLandImage" :src="`https://farm.juzhentech.com${item}`"
				:key="index" mode=""></image> -->
			<image :src="`https://farm.juzhentech.com${configDetail.BuyLandImage}`" mode=""></image>
		</view>
		<view class="bottom">
			<view class="btn" @click="open">
				<text class="pay">支付</text>
				<text class="money">
					(<text
						:style="{'text-decoration': configDetail.IsDiscount?'line-through':'none'}">{{configDetail.IsDiscount?`原价${configDetail.BuyLandPrice}`:configDetail.DiscountPrice}}</text><text
						class="discountPrice"
						v-if="configDetail.IsDiscount">{{`现价${configDetail.DiscountPrice}`}}</text>)
				</text>

			</view>
		</view>
		<uni-popup ref="popup" class="" type="bottom">
			<view class="popup">
				<view class="">
					<view class="title">
						<view class="">结算</view>
					</view>
					<view class="text-image">
						<image src="@/static/img/bg@3x.png" mode=""></image>
						<view class="text">
							<view class="">一号田</view>
							<view class="">手机号：{{phoneData}}</view>
							<view class="">
								￥{{configDetail.IsDiscount?configDetail.DiscountPrice:configDetail.BuyLandPrice}}</view>
						</view>
					</view>
				</view>
				<view class="middle">
					<view class="middle-title">购买土地权限</view>
					<view class="middle-text">
						<view class="">1、15平方米自留地</view>
						<view class="">2、按年定期蔬菜配送服务</view>
						<view class="">3、权益三内容</view>
						<view class="">4、权益四内容</view>
					</view>
				</view>
				<view class="bottom">
					<view class="btn" @click="payment">
						<text>结算</text>
					</view>
				</view>
				<view class="btn-bottom" @click="hide">
					再考虑一下
				</view>
			</view>
		</uni-popup>
	</view>
</template>

<script>
	import {
		GetCreateBuyLandOrderAjax,
		GetOrderPayAjax
	} from '@/apis/api/modules/buyland'
	export default {
		data() {
			return {
				payOrder: {},
				orderNo: null
			}
		},
		props: {
			phoneData: {
				default: {}
			}
		},
		computed: {
			configDetail() {
				console.log(this.$store.state.user.configDetail)
				return this.$store.state.user.configDetail || {}
			}
		},
		created() {
			this.configDetail.BuyLandImage?.split(',').filter((item) => item) ?? []
		},
		methods: {
			async open() {
				try {
					//创建购地订单号
					const result = await GetCreateBuyLandOrderAjax({
						token: true
					})
					// this.orderNo = 'La202212071623311686'
					this.orderNo = result.Result
					const payOrder = await GetOrderPayAjax({
						orderNo: result.Result,
						// orderNo: 'La202212071623311686',
						payType: 2,
						token: true
					})
					this.payOrder = payOrder.Result
				} catch (e) {
					//TODO handle the exception
				}
				this.$refs.popup.open()
			},
			hide() {
				this.$refs.popup.close()
			},
			payment() {
				let that = this
				setTimeout(() => {
					uni.requestPayment({
						"provider": 'wxpay',
						"signType": "RSA",
						"appId": that.payOrder.appId,
						"nonceStr": that.payOrder.nonceStr,
						"package": that.payOrder.package,
						"partnerId": that.payOrder.partnerId,
						"prepayId": that.payOrder.prepayId,
						"timeStamp": that.payOrder.timeStamp,
						"paySign": that.payOrder.paySign,
						success: function(res) {
							uni.showToast({
								title: '支付成功！',
								icon: 'success'
							})
							that.$refs.popup.close()
							that.$emit('paySuccess', that.orderNo)
							console.log('success:' + JSON.stringify(res));
						},
						fail: function(err) {
							uni.showToast({
								title: '支付失败！',
								icon: 'error'
							})
							console.log('fail:' + JSON.stringify(err));
						}
					}, 200)
				})

			}
		}
	}
</script>

<style scoped lang="scss">
	.box {
		// height: 100%;
		padding: 40upx 30upx 0;
		// display: flex;
		// flex-direction: column;
	}

	.buy {
		font-size: 40upx;
		font-weight: 500;
		color: #333333;
		line-height: 56upx;
		display: flex;
		justify-content: space-between;

		.text-right {
			text:nth-of-type(1) {
				width: 42upx;
				height: 56upx;
				font-size: 40upx;
				font-weight: 500;
				color: #00D08F;
				line-height: 56upx;
			}

			text:nth-of-type(2) {
				width: 20upx;
				height: 56upx;
				font-size: 40upx;
				font-weight: 500;
				color: #999990;
				line-height: 56upx;
			}

			text:nth-of-type(3) {
				width: 48upx;
				height: 56upx;
				font-size: 40upx;
				font-weight: 500;
				color: #333333;
				line-height: 56upx;
			}
		}
	}

	.content {
		// flex: 1;
		// overflow: auto;
		padding: 58upx 30upx 0;
		width: 630upx;
		height: 816upx;
		font-size: 30upx;
		font-weight: 400;
		color: #666666;
		line-height: 48upx;
		letter-spacing: 1upx;
	}

	.popup {
		background-color: #fff;
		border-top-left-radius: 30upx;
		border-top-right-radius: 30upx;
		padding: 30upx;

		.title {
			text-align: center;
			font-size: 34upx;
			font-weight: 500;
			color: #333333;
		}

		.text-image {
			padding: 40upx 0 30upx;
			display: flex;
			justify-content: start;
			align-items: center;

			.text {
				padding-left: 30upx;

				view:nth-of-type(1) {
					padding-bottom: 16upx;
					font-size: 34upx;
					font-weight: 500;
					color: #333333;
				}

				view:nth-of-type(2) {
					padding-bottom: 16upx;
					font-size: 32upx;
					font-weight: 400;
					color: #999999;
				}

				view:nth-of-type(3) {
					font-size: 28upx;
					font-weight: 500;
					color: #FEB042;
				}
			}

			image {
				width: 180upx;
				height: 180upx;
				border: 30upx;
			}
		}

	}

	.middle {
		padding: 20upx;
		background: #F7F7F7;
		border-radius: 20upx;

		.middle-title:nth-of-type(1) {
			font-size: 28upx;
			font-weight: 500;
			color: #333333;
			padding-bottom: 20rpx;
		}

		.middle-text {
			font-size: 28upx;
			font-weight: 400;
			color: #666666;

			view {
				padding-bottom: 20rpx;
			}

			view:nth-of-type(4) {
				padding-bottom: 0rpx;
			}
		}
	}

	.btn-bottom {
		padding: 30upx 0 10upx;
		text-align: center;
		font-size: 28upx;
		font-weight: 400;
		color: #999999;
		line-height: 40upx;
	}

	.btn {
		margin-top: 60upx;
		width: 690upx;
		height: 104upx;
		background: #00D08F;
		border-radius: 52upx;
		backdrop-filter: blur(20upx);
		font-size: 32upx;
		font-weight: 500;
		color: #FFFFFF;
		line-height: 104upx;
		text-align: center;

		.pay {
			font-size: 32upx;
			font-weight: 500;
			color: #FFFFFF;
			line-height: 44upx;
		}

		.money {
			padding-left: 20upx;
			font-size: 32upx;
			font-weight: 600;
			color: #FFCD86;
			line-height: 44upx;
			//text-decoration: line-through;
		}

		.discountPrice {
			padding-left: 6rpx;
		}
	}
</style>
